import React from "react";
import { Item } from "semantic-ui-react";

const educations = [
  {
    nama: "Politeknik Tri Mitra Karya Mandiri",
    jurusan: "Computer Engineering (Diploma III)",
    tahun: "2012 - 2015"
  },
  {
    nama: "SMKN 1 Indramayu",
    jurusan: "Multimedia",
    tahun: "2009 - 2012"
  },
  {
    nama: "SMPN 2 Sukagumiwang",
    jurusan: "Indramayu",
    tahun: "2006 - 2009"
  },
  {
    nama: "SDN 1 Beduyut",
    jurusan: "Indramayu",
    tahun: "2000 - 2006"
  }
];

const Education = () => (
  <Item.Group divided>
    {educations.map((item, i) => (
      <Item key={i}>
        <Item.Content>
          <Item.Header as="a">{item.nama}</Item.Header>
          <Item.Meta>
            <span className="cinema">{item.jurusan} </span>
          </Item.Meta>
          <Item.Meta>
            <span>{item.tahun}</span>
          </Item.Meta>
        </Item.Content>
      </Item>
    ))}
  </Item.Group>
);

export default Education;



